.app {
    max-width: 560px;
    padding: 50px 0;
    margin: auto;
    .headerWrapper {
        display: flex;
        justify-content: center;
        max-width: 375px;
        max-height: 50px;
        margin: auto;
        border: 1px solid black;
        .header {
            margin: auto;
            font-size: 28px;
            font-weight: bold;
        }
    }

    .content {
        display: flex;
        .pokemonList {
            max-width: 340px;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            margin-top: 10px;
        }

        .typeFilter {
            margin-left: 357px;
            position: absolute;
        }

        .pokemonInfo {
            margin: 202px 0 0 27px;
        }
    }
}

@media (max-width: 480px) {
    .pokemonList {
    background-color: red;
        //    max-width: 110px;
    }
}
